import * as React from "react";
import { createRoot } from "react-dom/client";
import ScrollToTop from "react-scroll-to-top";
import App from "./App";
import Allabout from "./About/Allabout";
import {
  createBrowserRouter,
  RouterProvider,
  Route,
  Link,
} from "react-router-dom";
import Allshop from "./Shop/Allshop";
import Allcontact from "./Contact/Allcontact";
import Allproduct from "./Product/Allproduct"
import Allcart from "./Cart/Allcart";
import {Alldata} from "./Data/Alldata";
const router = createBrowserRouter([
  {
    path: "/",
    element:<App/>
  },
  {
    path: "about",
    element:<Allabout/>
  },
  {
    path: "shop",
    element:<Allshop/>
  },
{
    path: "contact",
    element:<Allcontact/>
  },
{
    path: "product",
    element:<Allproduct/>
  },
{
    path: "cart",
    element:<Allcart/>
  },
  
]);

createRoot(document.getElementById("root")).render(
  <Alldata>
  <RouterProvider router={router} />

  <ScrollToTop
     smooth 
     viewBox="0 0 384 512"
     svgPath="M214.6 41.4c-12.5-12.5-32.8-12.5-45.3 0l-160 160c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0L160 141.2 160 448c0 17.7 14.3 32 32 32s32-14.3 32-32l0-306.7L329.4 246.6c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3l-160-160z"
      color="white"
      style={{backgroundColor:"#3b5d50" , borderRadius:"50%"}}
     />
 
  </Alldata>
);
