
import React from 'react';
import {CartProvider, useCart} from "react-use-cart";
import {Link} from "react-router"
import Nav from "../Component/Nav";
import Header from '../Component/Header';
import Footer from '../Component/Footer';




function Cart() {
    const {
      isEmpty,
      totalUniqueItems,
      totalItems,
      cartTotal,
      items,
      updateItemQuantity,
      removeItem,
      emptyCart
    } = useCart();
  
    if (isEmpty) return <h4 className="text-center">Your cart is empty</h4>;
  
    return (
 <div>

<main class="page">
	 	<section class="shopping-cart dark">
	 		<div class="container">
		        <div class="block-heading">
		          <h2>Shopping Cart ({totalItems})</h2>
		          
		        </div>
		        <div class="content">
	 				<div class="row">
	 					<div class="col-md-12 col-lg-8">
	 						<div class="items">
                {items.map((item)=>(

				 				<div class="product">
				 					<div class="row">
					 					<div class="col-md-3">
					 						<img class="img-fluid mx-auto d-block image" src={item.images[0]}/>
					 					</div>
					 					<div class="col-md-8">
					 						<div class="info">
						 						<div class="row">
							 						<div class="col-md-5 product-name">
							 							<div class="product-name">
                            
								 							<h3>id: {item.id}</h3>
								 							<p>Price: {item.price}</p>
								 							
									 					</div>
							 						</div>
							 						<div class="col-md-4 quantity">
							 							<label for="quantity">Quantity:</label>
							 							<br></br>
                            {item.quantity}
                            <br></br>
                            <button className="btn btn-warning" onClick={()=>updateItemQuantity(item.id,item.quantity+1)}>+</button>
                        <button  className="btn btn-success mx-2"onClick={()=>updateItemQuantity(item.id,item.quantity-1)}>-</button>
                        <button className="btn btn-danger" onClick={()=>removeItem(item.id)}>&times;</button>

							 						</div>
							 						<div class="col-md-3 price">
							 							<span>${Math.ceil(item.price * item.quantity)}</span>
							 						</div>
							 					</div>
							 				</div>
					 					</div>
					 				</div>
				 				</div>
                ))}

				 			</div>
			 			</div>
			 			<div class="col-md-12 col-lg-4">
			 				<div class="summary">
			 					<h3>Summary</h3>
			 					<div class="summary-item"><span class="text">Subtotal</span><span class="price">{Math.ceil(cartTotal)}</span></div>
			 					<div class="summary-item"><span class="text">Discount</span><span class="price">$0</span></div>
			 					<div class="summary-item"><span class="text">Shipping</span><span class="price">$40</span></div>
			 					<div class="summary-item"><span class="text">Total</span><span class="price">${Math.ceil(cartTotal+40)}</span></div>
			 					<button type="button" class="btn btn-primary btn-lg btn-block">Checkout</button>
				 			</div>
			 			</div>
		 			</div> 
		 		</div>
	 		</div>
		</section>
	</main>












 </div>

     
      
    );
  }
function Allcart() {
  return (
    <CartProvider>
        <Nav></Nav>
        <Header/>
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <Cart></Cart>
        <Footer/>
    </CartProvider>
  )
}

export default Allcart